a {
  color: #f052a1;
  padding: 0;
  text-decoration: none;
  display: block;
}

a:hover {
  text-decoration: underline;
}

.search-results {
  margin-top: 8px;
}

.search-result-item {
  --text-color: #333;
  --highlight-color: #333;
  --search-result-item-bg: #f8f9fa;
  background: #f8f9fa;
  border-left: 4px solid #8bc34a;
  border-radius: 6px;
  margin-bottom: 10px;
  padding: 5px 15px;
}

.search-result-item.dark-mode {
  --text-color: #e0e0e0;
  --highlight-color: #333;
  --search-result-item-bg: #171718;
  background: var(--search-result-item-bg);
}

.search-result-item.dark-mode .search-result-excerpt {
  color: var(--text-color);
}

.search-result-item.dark-mode .search-result-excerpt .highlight {
  color: var(--highlight-color);
}

.search-result-title {
  margin-bottom: 8px;
  font-size: 1.2em;
}

.search-result-excerpt {
  color: #555;
  margin-bottom: 10px;
}

.search-result-count {
  margin-bottom: 10px;
  font-weight: bold;
}

.search-loading {
  color: #666;
  text-align: center;
  margin-bottom: 4px;
  font-size: 1.1em;
  font-weight: bold;
}

.search-loading .fa-spin {
  margin-right: 8px;
  padding-top: 1px;
  padding-right: 1px;
}

.search-result-item.dark-mode .search-loading {
  color: #e0e0e0;
}

.search-loading .fa-spin {
  animation: 1s steps(8, end) infinite tick-rotate;
}

@keyframes tick-rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.highlight {
  color: #333;
  background-color: #ffeb3b;
  padding: 0 2px;
  font-weight: bold;
}

#doc {
  width: 100%;
  min-width: unset;
  max-width: unset;
  min-height: unset;
  flex: 1;
  overflow-y: hidden;
}
