a {
  text-decoration: none;
  display: block;
  padding: 0;
  color: #f052a1;
}
a:hover {
  text-decoration: underline;
}

.search-results {
  margin-top: 8px;
}

.search-result-item {
  margin-bottom: 10px;
  padding: 5px 15px;
  border-radius: 6px;
  background: #f8f9fa;
  border-left: 4px solid #8BC34A;
  --text-color: #333;
  --highlight-color: #333;
  --search-result-item-bg: #f8f9fa;
}
.search-result-item.dark-mode {
  --text-color: #e0e0e0;
  --highlight-color: #333;
  --search-result-item-bg: #171718;
  background: var(--search-result-item-bg);
}

.search-result-item.dark-mode .search-result-excerpt {
  color: var(--text-color);
}
.search-result-item.dark-mode .search-result-excerpt .highlight {
  color: var(--highlight-color);
}

.search-result-title {
  font-size: 1.2em;
  margin-bottom: 8px;
}

.search-result-excerpt {
  color: #555;
  margin-bottom: 10px;
}

.search-result-count {
  font-weight: bold;
  margin-bottom: 10px;
}

.search-loading {
  margin-bottom: 4px;
  color: #666;
  font-weight: bold;
  font-size: 1.1em;
  text-align: center;
}
.search-loading .fa-spin {
  margin-right: 8px;
  padding-top: 1px;
  padding-right: 1px;
}
.search-result-item.dark-mode .search-loading {
  color: #e0e0e0;
}
.search-loading .fa-spin {
  animation: tick-rotate 1s steps(8) infinite;
}
@keyframes tick-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.highlight {
  background-color: #ffeb3b;
  color: #333;
  font-weight: bold;
  padding: 0 2px;
}

#doc {
  width: 100%;
  min-width: unset;
  max-width: unset;
  min-height: unset;
  flex: 1;
  overflow-y: hidden;
}
